const initialState = {
    artists: [],
    songs: []
}

const typeAheadReducer = (state = initialState, action) => {
    switch (action.type) {
        case "SEARCH_ARTIST":
            state = {
                ...state, 
                artists: [...action.payload]
            }
            break;
        case "SEARCH_SONG":
            state = {
                ...state,
                songs: [...action.payload]
            }
            break;
        default: 
            break;
    }
    return state;
};

export default typeAheadReducer;


