import Axios from 'axios';

const apikey = '6761c33acb16243b8b0ad1613f4311cf';
const baseurl = 'http://ws.audioscrobbler.com/2.0/?format=json';

export function searchArtist(query){
    return (distpatch) => {
        if (query.length >= 3){
            Axios.get(`${baseurl}&method=artist.search&artist=${query}&api_key=${apikey}&limit=3`)
            .then((v) => {
                distpatch({
                    type: 'SEARCH_ARTIST',
                    payload: v.data.results.artistmatches.artist.slice(0,3)
                })
            }).catch((v)=>{
                console.warn(v);
            })
        }
    }
}

export function searchSong(query, artist, limit = 3) {
    return (distpatch) => {
        if (query.length >= 1) {
            Axios.get(`${baseurl}&method=track.search&track=${query}&artist=${artist}&api_key=${apikey}&limit=${limit}`)
            .then((v) => {
                distpatch({
                    type: 'SEARCH_SONG',
                    payload: v.data.results.trackmatches.track
                })
            }).catch((v) => {
                console.warn(v);
            })
        }
    }
}


