import React, { Component } from 'react';
import { withRouter } from "react-router-dom";
// import Suggestion from './Suggestion';
import { connect } from 'react-redux';
import { searchArtist, searchSong } from '../actions/typeAheadActions';

class SearchForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            artist: this.props.artist,
            song: this.props.song,
            submitEnabled: false
        }
    }

    componentDidUpdate(previousProps, previousState){
        if (previousProps.match.url !== this.props.match.url && previousProps !== this.props) {
            const { match } = this.props;
            if (match != null && match.params.artist != null && match.params.song != null) {
                this.setState({ artist: match.params.artist, song: match.params.song, submitEnabled: true});
            }else{
                this.setState({artist: '', song: ''})
            }
        }

        if (previousState.artist !== this.state.artist || previousState.song !== this.state.song){
            const { artist, song } = this.state;
            if (artist !== "" && song !== "") {
                this.setState({ submitEnabled: true });
            } else {
                this.setState({ submitEnabled: false });
            }
        }
    }

    updateInputValue(event) {
        let {artist, song} = this.state;
        switch (event.target.name) {
            case 'artist':
                artist = event.target.value;
                this.props.searchArtist(`${artist}`);
                this.setState({artist});
                break;
            case 'song':
                song = event.target.value;
                this.setState({song});
                this.props.searchSong(event.target.value, this.state.artist);
                break;
            default:
                break;
        }
    }

    onArtistClick(i){
        const artist = this.props.type.artists[i];
        this.setState({ artist: artist.name, song: ''});
        this.props.searchSong(' ', artist.name);
    }
    
    onSongClick(i) {
        this.setState({ song: this.props.type.songs[i].name });
    }

    submitForm(event){
        event.preventDefault();
        if (this.state.artist   !== "" && this.state.song !== "") {
            this.props.history.push('/' + this.state.artist + '/' + this.state.song);
        }
    }

    render() { 
        return (<form onSubmit={this.submitForm.bind(this)}>
                    <div className="artist-input subsearch">
                        <input autoFocus value={this.state.artist} onChange={this.updateInputValue.bind(this)} name='artist' className="form-control" type="text" placeholder="Artist" />
                        {/* <Suggestion list={this.props.type.artists} onItemClick={this.onArtistClick.bind(this)}/> */}
                    </div>
                    <div className="song-input subsearch">
                        <input autoFocus value={this.state.song} onChange={this.updateInputValue.bind(this)} name='song' className="form-control" type="text" placeholder="Name of the song" />
                        {/* <Suggestion list={this.props.type.songs} onItemClick={this.onSongClick.bind(this)}/> */}
                    </div>
                    <button autoFocus type="submit" className="btn btn-light btn-block" disabled={!this.state.submitEnabled}>Search</button>
                </form>
         )
    }
}

const mapStateToProps = (state) => {
    return {
        type: state.type 
    }
}

const mapDistpatchToProps = (distpatch) => {
    return {
        searchArtist: (query) => {
            distpatch(searchArtist(query));
        },
        searchSong: (query, artist) => {
            distpatch(searchSong(query, artist));
        }
    }
}

export default connect(mapStateToProps, mapDistpatchToProps)(withRouter(SearchForm));


